import React from "react";
import Aboutimg from '../Images/img1.png';
import '../Css/HomeAbout.css';

const About=()=>{
    return(
        <>
        <div className="mainsection">
            <div className="contentBox">
          <h1>More About Repair Made Easy</h1>
          <p>This offering includes custom designed technology plans, networks:computer systems, networking servers as well as internet access. As the business world moved into the Windows 10 and 11 environments, software user training has become and incresingly important service available in Shri Systems.</p>
          </div>   
          <div className="imgcontainer">
            <img src={Aboutimg} alt="about"/>
        </div>
          </div>
        </>
    )
}

export default About;